type MediaDeviceKind = "audioinput" | "audiooutput" | "videoinput";

interface MediaDeviceInfo {
    readonly deviceId: string;
    readonly groupId: string;
    readonly kind: MediaDeviceKind;
    readonly label: string;
    toJSON(): any;
}

interface MediaDevicesEventMap {
    devicechange: Event;
}

interface DisplayMediaStreamConstraints {
    preferCurrentTab?: boolean; // Not standard yet
    audio?: boolean | MediaTrackConstraints;
    video?: boolean | MediaTrackConstraints;
}

interface MediaTrackConstraintSet {
    aspectRatio?: ConstrainDouble;
    autoGainControl?: ConstrainBoolean;
    channelCount?: ConstrainULong;
    deviceId?: ConstrainDOMString;
    echoCancellation?: ConstrainBoolean;
    facingMode?: ConstrainDOMString;
    frameRate?: ConstrainDouble;
    groupId?: ConstrainDOMString;
    height?: ConstrainULong;
    latency?: ConstrainDouble;
    noiseSuppression?: ConstrainBoolean;
    sampleRate?: ConstrainULong;
    sampleSize?: ConstrainULong;
    suppressLocalAudioPlayback?: ConstrainBoolean;
    width?: ConstrainULong;
}

interface MediaTrackConstraints extends MediaTrackConstraintSet {
    advanced?: MediaTrackConstraintSet[];
}

interface MediaStreamConstraints {
    audio?: boolean | MediaTrackConstraints;
    peerIdentity?: string;
    preferCurrentTab?: boolean;
    video?: boolean | MediaTrackConstraints;
}

interface MediaTrackSupportedConstraints {
    whiteBalanceMode: boolean;
    exposureMode: boolean;
    focusMode: boolean;
    pointsOfInterest: boolean;

    exposureCompensation: boolean;
    colorTemperature: boolean;
    iso: boolean;

    brightness: boolean;
    contrast: boolean;
    saturation: boolean;
    sharpness: boolean;
    focusDistance: boolean;
    zoom: boolean;
    torch: boolean;
}

interface MediaDevices {
    ondevicechange: ((this: MediaDevices, ev: Event) => any) | null;
    enumerateDevices(): Promise<MediaDeviceInfo[]>;
    getDisplayMedia(constraints?: DisplayMediaStreamConstraints): Promise<MediaStream>;
    getSupportedConstraints(): MediaTrackSupportedConstraints;
    getUserMedia(constraints?: MediaStreamConstraints): Promise<MediaStream>;
    addEventListener<K extends keyof MediaDevicesEventMap>(
        type: K,
        listener: (this: MediaDevices, ev: MediaDevicesEventMap[K]) => any
    ): void;
    addEventListener(type: string, listener: EventListenerOrEventListenerObject): void;
    removeEventListener<K extends keyof MediaDevicesEventMap>(
        type: K,
        listener: (this: MediaDevices, ev: MediaDevicesEventMap[K]) => any,
        options?: boolean | EventListenerOptions
    ): void;
    removeEventListener(
        type: string,
        listener: EventListenerOrEventListenerObject,
        options?: boolean | EventListenerOptions
    ): void;
}

const getBlobFromMediaStream = (stream: MediaStream): Promise<Blob> => {
    if ("ImageCapture" in window) {
        const track = stream.getVideoTracks()[0];
        const imageCapture = new ImageCapture(track);
        return new Promise((resolve, reject) => {
            imageCapture
                .grabFrame()
                .then(bitmap => {
                    track.stop();
                    const canvas = document.createElement("canvas");
                    canvas.width = bitmap.width;
                    canvas.height = bitmap.height;
                    const context = canvas.getContext("2d");
                    if (!context) {
                        reject("No context");
                        return;
                    }
                    context.drawImage(bitmap, 0, 0, bitmap.width, bitmap.height);
                    canvas.toBlob((blob: Blob) => (blob ? resolve(blob) : reject(new Error("No blob"))), "image/png");
                })
                .catch(error => reject(error));
        });
    } else {
        return new Promise((resolve, reject) => {
            const video = document.createElement("video");
            const canvas = document.createElement("canvas");
            const context = canvas.getContext("2d");
            video.srcObject = stream;
            video.addEventListener("loadeddata", () => {
                const { videoWidth, videoHeight } = video;
                canvas.width = videoWidth;
                canvas.height = videoHeight;
                if (!context) {
                    reject("No context");
                    return;
                }
                try {
                    context.drawImage(video, 0, 0, videoWidth, videoHeight);
                    canvas.toBlob((blob: Blob) => {
                        video.pause();
                        if (!blob) {
                            reject(new Error("No blob"));
                        }
                        resolve(blob);
                    }, "image/png");
                } catch (error) {
                    reject(error);
                }
            });
            video.play();
        });
    }
};

const getCaptureImage = async (): Promise<Blob> => {
    return new Promise((resolve, reject) => {
        ((navigator.mediaDevices as unknown) as MediaDevices)
            .getDisplayMedia({ preferCurrentTab: true })
            .then((stream: MediaStream) => {
                getBlobFromMediaStream(stream)
                    .then((blob: Blob) => (blob ? resolve(blob) : reject(new Error("No blob"))))
                    .catch((e: Error) => reject(e));
            })
            .catch((e: Error) => reject(e));
    });
};

const getCaptureVideo = async (): Promise<void> => {
    return new Promise((resolve, reject) => {
        ((navigator.mediaDevices as unknown) as MediaDevices)
            .getDisplayMedia({ preferCurrentTab: true })
            .then((stream: MediaStream) => {
                const recordedChunks: Blob[] = [];
                const options = { mimeType: "video/webm; codecs=vp9" };
                const mediaRecorder = new (window as any).MediaRecorder(stream, options);
                mediaRecorder.ondataavailable = (event: any) => {
                    if (event.data.size > 0) {
                        recordedChunks.push(event.data);
                        const blob = new Blob(recordedChunks, {
                            type: "video/webm"
                        });
                        const url = URL.createObjectURL(blob);
                        const a = document.createElement("a") as HTMLAnchorElement;
                        document.body.appendChild(a);
                        a.style.display = "none";
                        a.href = url;
                        a.download = "test.webm";
                        a.click();
                        window.URL.revokeObjectURL(url);
                    }
                };
                mediaRecorder.start();
                setTimeout(() => {
                    mediaRecorder.stop();
                }, 10000);
                resolve();
            })
            .catch((e: Error) => reject(e));
    });
};

const isCaptureAvailable = (): boolean => {
    if (!navigator.mediaDevices) {
        return false;
    }
    const mediaDevices = (navigator.mediaDevices as unknown) as MediaDevices;
    if (!mediaDevices.getDisplayMedia) {
        return false;
    }
    return true;
};

export { getCaptureImage, getCaptureVideo, isCaptureAvailable };
