import {
    FEEDBACK_COMMENT_MAX_LENGTH,
    FEEDBACK_EMAIL_MAX_LENGTH,
    FEEDBACK_TITLE_MAX_LENGTH,
    SCOPE_PREFIX
} from "../config";
import React, { Fragment, ReactElement, createElement, useEffect, useRef, useState } from "react";
import { validateEmail, validateFeedbackComment, validateFeedbackTitle, validateImage } from "../utils/validation";

import CloseIcon from "../resources/CloseIcon";
import DeleteScreenshotIcon from "../resources/DeleteScreenshotIcon";
import { FeedbackFormProps } from "../../typings/FeedbackFormProps";
import FormGroup from "./FormGroup";
import PreviewScreenshotIcon from "../resources/PreviewScreenshotIcon";
import ScreenshotButtonGroup from "./ScreenshotButtonGroup";
import { UserInfo } from "../../typings/UserInfo";
import classNames from "classnames";
import { useDetermineUserType } from "../hooks/useDetermineUserType";
import { useHandleUserEmailInfo } from "../hooks/useHandleUserEmailInfo";
import { useSetCursorFocus } from "../hooks/useSetCursorFocus";
import { useSubmitFeedback } from "../hooks/useSubmitFeedback";
import { useUploadScreenshot } from "../hooks/useUploadScreenshot";

const componentName = `${SCOPE_PREFIX}-feedback-form`;

const FeedbackForm = ({
    widgetSettings,
    formData,
    translation,
    updateFormData,
    onUpdateScreenshot,
    takeScreenshot,
    onShowLightbox,
    showLightbox,
    cancel,
    succeeded,
    failed
}: FeedbackFormProps): ReactElement => {
    const defaultUserData: UserInfo = { name: "", email: formData.email, userRoles: [] };
    const [userData, setUserData] = useState<UserInfo>(defaultUserData);
    const [feedbackCommentValid, setFeedbackCommentValid] = useState<boolean>(true);
    const [feedbackTitleValid, setFeedbackTitleValid] = useState<boolean>(true);
    const [feedbackEmailValid, setFeedbackEmailValid] = useState<boolean>(
        formData && formData.email ? validateEmail(formData.email) : true
    );
    const [uploadFileValid, setUploadFileValid] = useState<boolean>(true);

    const defaultShowEmail = () => {
        if (widgetSettings.userInfoEntityDS && widgetSettings.emailAddressAttribute) return false;
        if (formData && formData.email && feedbackEmailValid) return false;
        return true;
    };
    const [showEmail, setShowEmail] = useState(defaultShowEmail());
    const [submitInProgress, setSubmitInProgress] = useState<boolean>(false);
    const formOuter = useRef<HTMLDivElement | null>(null);
    const titleInput = useRef<HTMLInputElement | null>(null);
    const descriptionInput = useRef<HTMLTextAreaElement>(null);
    const screenshotButton = useRef<HTMLButtonElement>(null);
    const emailInput = useRef<HTMLInputElement>(null);
    const { title, description, email, screenshot } = formData;
    const submitButton = useRef<HTMLButtonElement | null>(null);
    const firstFocusableElementForm = useRef<HTMLElement | null>(null);
    const lastFocusableElementForm = useRef<HTMLElement | null>(null);
    const handleUserEmailInfo = useHandleUserEmailInfo(
        setFeedbackEmailValid,
        setUserData,
        userData,
        updateFormData,
        formData
    );
    const setCursorFocus = useSetCursorFocus(
        title,
        titleInput,
        description,
        descriptionInput,
        screenshot,
        screenshotButton,
        emailInput,
        email,
        feedbackEmailValid
    );
    const uploadScreenshot = useUploadScreenshot(validateImage, setUploadFileValid, onUpdateScreenshot);
    const onSubmit = useSubmitFeedback(
        setFeedbackCommentValid,
        setFeedbackTitleValid,
        setFeedbackEmailValid,
        showEmail,
        setShowEmail,
        titleInput,
        descriptionInput,
        emailInput,
        setSubmitInProgress,
        widgetSettings,
        screenshot,
        updateFormData,
        onUpdateScreenshot,
        succeeded,
        failed
    );

    useDetermineUserType(setShowEmail, widgetSettings, setUserData);

    const keyDownAction = (e: KeyboardEvent): void => {
        if (e.key === "Escape") {
            if (showLightbox) {
                onShowLightbox(false);
            } else {
                cancel();
            }
        }

        if (e.key === "Tab") {
            if (e.shiftKey) {
                if (e.target === firstFocusableElementForm.current) {
                    e.preventDefault();
                    lastFocusableElementForm.current?.focus();
                }
            } else {
                if (e.target === lastFocusableElementForm.current) {
                    e.preventDefault();
                    firstFocusableElementForm.current?.focus();
                }
            }
        }
    };

    // If no widgetSettings.emailAddressAttribute, always show email input.
    useEffect(() => {
        if (userData.email === "" || !widgetSettings.emailAddressAttribute) {
            setShowEmail(true);
        } else {
            setShowEmail(false);
        }
    }, [userData]);

    useEffect(() => {
        const focusableElement = Array.from<HTMLElement>(formOuter.current?.querySelectorAll("[tabindex]") ?? []);

        firstFocusableElementForm.current = focusableElement[0];
        lastFocusableElementForm.current = focusableElement[focusableElement.length - 1];

        firstFocusableElementForm.current.focus();
    }, [firstFocusableElementForm, lastFocusableElementForm]);

    useEffect(() => {
        document.addEventListener("keydown", keyDownAction);
        setCursorFocus();

        return (): void => {
            document.removeEventListener("keydown", keyDownAction);
        };
    }, []);

    const onTitleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
        const title = e.target.value;
        const isValid = validateFeedbackTitle(title);

        if (isValid) {
            setFeedbackTitleValid(isValid);
        }

        updateFormData({ ...formData, title, timestamp: new Date().toString() });
    };

    const onTitleBlur = (e: { target: { value: string } }): void => {
        if (validateFeedbackTitle(e.target.value)) {
            setFeedbackTitleValid(true);
        }
    };

    const onDescriptionChange = (e: React.ChangeEvent<HTMLTextAreaElement>): void => {
        setFeedbackCommentValid(validateFeedbackComment(e.target.value));
        updateFormData({ ...formData, description: e.target.value, timestamp: new Date().toString() });
    };

    const onEmailBlur = (e: { target: { value: string } }): void => {
        const emailValid = validateEmail(formData.email ? formData.email : e.target.value);
        if (emailValid) {
            setFeedbackEmailValid(emailValid);
        }
    };

    const onDeleteScreenshot = (): void => {
        updateFormData({
            ...formData,
            screenshot: "",
            timestamp: new Date().toString()
        });
    };

    const onClear = (): void => {
        updateFormData({ title: "", description: "", email: "", screenshot: "", timestamp: "" });
        onUpdateScreenshot("");
    };

    const onSubmitFeedback = (): void => {
        if (submitInProgress) {
            return;
        }

        onSubmit({
            title,
            description,
            userData
        });
    };

    return (
        <div
            className={classNames(componentName, `${SCOPE_PREFIX}-dialog`)}
            data-html2canvas-ignore="true"
            ref={formOuter}
        >
            <button className={`${SCOPE_PREFIX}-close-button`} onClick={cancel} aria-label="Close button" tabIndex={0}>
                <CloseIcon className={`${SCOPE_PREFIX}-close-button__icon`} />
            </button>

            <div className="mxfeedback-dialog__header">
                <h4 className="mxfeedback-dialog__title">{translation.Modal_Title}</h4>
                <p className="mxfeedback-dialog__body-text">{translation.Modal_Introduction_Text}</p>
            </div>

            <FormGroup
                id="mxfeedback_feedback_title"
                errorText={translation.Required_Field}
                labelText={translation.Subject}
                hasError={!feedbackTitleValid}
                className={`${SCOPE_PREFIX}__input-text`}
            >
                <input
                    className="form-control"
                    id="mxfeedback_feedback_title_input"
                    ref={titleInput}
                    type="text"
                    value={title}
                    required={true}
                    name="feedback_title"
                    placeholder={translation.Summarize_Feedback_Item}
                    maxLength={FEEDBACK_TITLE_MAX_LENGTH}
                    onChange={onTitleChange}
                    onBlur={onTitleBlur}
                    aria-labelledby="mxfeedback_feedback_title_label"
                    data-testid="title"
                    autoFocus
                    tabIndex={0}
                />
            </FormGroup>

            <FormGroup
                id="mxfeedback_feedback_description"
                hasError={!feedbackCommentValid}
                labelText={translation.Description}
                tooltipText={translation.Add_as_many_details_as_you_can}
                errorText={translation.Description_Error}
                className={`${SCOPE_PREFIX}__input-textarea`}
            >
                <textarea
                    className="form-control mx-textarea-input mx-textarea-noresize"
                    id="mxfeedback_feedback_description_input"
                    ref={descriptionInput}
                    value={description}
                    name="feedback_description"
                    placeholder={translation.Description_Placeholder}
                    maxLength={FEEDBACK_COMMENT_MAX_LENGTH}
                    rows={4}
                    cols={50}
                    onChange={onDescriptionChange}
                    aria-labelledby="mxfeedback_feedback_description_label"
                    data-testid="description"
                    tabIndex={0}
                />
            </FormGroup>

            {!!widgetSettings.allowScreenshot && (
                <Fragment>
                    <FormGroup
                        id="mxfeedback_feedback_take_screenshot"
                        hasError={!uploadFileValid}
                        labelText={translation.Attachment}
                        tooltipText={translation.Use_Screenshot_mode_to_take_a_snapshot}
                        errorText={translation.FileSize}
                    >
                        <ScreenshotButtonGroup
                            takeScreenshot={takeScreenshot}
                            translation={translation}
                            uploadScreenshot={uploadScreenshot}
                            ref={screenshotButton}
                        />
                    </FormGroup>

                    {screenshot && (
                        <Fragment>
                            <div className={`${SCOPE_PREFIX}-screenshot-preview`}>
                                <img
                                    className={`${SCOPE_PREFIX}-screenshot-preview__image`}
                                    src={screenshot}
                                    alt={translation.Image_Alt_Text}
                                />
                                <button
                                    onClick={() => onShowLightbox(true)}
                                    className={`${SCOPE_PREFIX}-screenshot-preview__overlay`}
                                    id="mxfeedback_feedback_preview_overlay"
                                    tabIndex={0}
                                >
                                    <PreviewScreenshotIcon
                                        className={`${SCOPE_PREFIX}-screenshot-preview__preview-icon`}
                                    />
                                </button>
                                <DeleteScreenshotIcon
                                    className={`${SCOPE_PREFIX}-screenshot-preview__delete-icon`}
                                    onClick={onDeleteScreenshot}
                                />
                            </div>
                        </Fragment>
                    )}
                </Fragment>
            )}

            {showEmail && (
                <FormGroup
                    id="mxfeedback_feedback_email"
                    hasError={!feedbackEmailValid}
                    labelText={translation.Email_Address}
                    tooltipText={translation.Your_email_address_is_optional}
                    errorText={translation.Email_Invalid}
                    className={`${SCOPE_PREFIX}__input-text`}
                >
                    <input
                        className="form-control"
                        id="mxfeedback_feedback_email_input"
                        ref={emailInput}
                        type="text"
                        value={email}
                        name="comment_email"
                        placeholder={translation.Email_Address_Placeholder}
                        maxLength={FEEDBACK_EMAIL_MAX_LENGTH}
                        onChange={handleUserEmailInfo}
                        onBlur={onEmailBlur}
                        aria-labelledby="mxfeedback_feedback_email_label"
                        data-testid="email"
                        tabIndex={0}
                    />
                </FormGroup>
            )}

            <div className={`${SCOPE_PREFIX}-button-group`}>
                <button
                    className={classNames(
                        "btn mx-button btn-secondary",
                        `${SCOPE_PREFIX}-button ${SCOPE_PREFIX}__btn-text`,
                        `${componentName}__cancel-button`
                    )}
                    type="button"
                    onClick={cancel}
                    tabIndex={0}
                >
                    {translation.Cancel}
                </button>
                <button
                    className={`btn mx-button btn-secondary ${SCOPE_PREFIX}-button ${SCOPE_PREFIX}__btn-bordered`}
                    type="button"
                    onClick={onClear}
                    tabIndex={0}
                >
                    {translation.Clear}
                </button>
                <button
                    className={`btn mx-button btn-primary ${SCOPE_PREFIX}-button`}
                    ref={submitButton}
                    data-testid="submit"
                    type="button"
                    disabled={submitInProgress}
                    onClick={onSubmitFeedback}
                    tabIndex={0}
                >
                    {submitInProgress ? `${translation.Sending_feedback}` : `${translation.Submit}`}
                </button>
            </div>
        </div>
    );
};

export default FeedbackForm;
