import {
    PermissionType,
    checkClipboardPermission,
    imageToBase64,
    isClipboardAvailable,
    readImageFromClipboard
} from "../utils/screenshot/imageUtils";
import { ReactElement, createElement, useEffect, useRef, useState } from "react";
import { getCaptureImage, isCaptureAvailable } from "../utils/screenshot/capture";

import { SCOPE_PREFIX } from "../config";
import TakeScreenshotIcon from "../resources/TakeScreenshotIcon";
import thirdPartyCapture from "../utils/screenshot/thirdPartyCapture";
import classNames from "classnames";

const ScreenshotContainer = ({
    onSave,
    onCancel,
    onMinimize,
    scrollableAreaSelector,
    foreignObjectRendering,
    translation
}: {
    onSave: (image: string) => void;
    onCancel: () => void;
    onMinimize: () => void;
    scrollableAreaSelector: string;
    foreignObjectRendering: boolean;
    translation: Record<string, string | undefined>;
}): ReactElement => {
    const [takingScreenshot, setTakingScreenshot] = useState(false);
    const screenshotOuter = useRef<HTMLDivElement | null>(null);
    const firstFocusableElementScreenshot = useRef<HTMLElement | null>(null);
    const lastFocusableElementScreenshot = useRef<HTMLElement | null>(null);

    const takeAlternateScreenshot = () => {
        thirdPartyCapture(scrollableAreaSelector, foreignObjectRendering)
            .then((screenshot: string) => {
                onSave(screenshot);
                setTakingScreenshot(false);
            })
            .catch((error: Error) => {
                console.error(error);
                setTakingScreenshot(false);
                onSave("");
            });
    };

    const takeScreenshot = (): void => {
        if (takingScreenshot) {
            return;
        }
        setTakingScreenshot(true);
        if (isCaptureAvailable()) {
            onMinimize();
            getCaptureImage()
                .then(blob =>
                    imageToBase64(blob)
                        .then((screenshot: string) => {
                            setTakingScreenshot(false);
                            onSave(screenshot);
                        })
                        .catch((error: Error) => {
                            console.error(error);
                            setTakingScreenshot(false);
                            takeAlternateScreenshot();
                        })
                )
                .catch((error: Error) => {
                    console.error(error);
                    setTakingScreenshot(false);
                    if (error.name !== "NotAllowedError") {
                        takeAlternateScreenshot();
                    }
                });
        } else {
            takeAlternateScreenshot();
        }
    };

    const pasteScreenshot = (): void => {
        if (!isClipboardAvailable()) {
            return;
        }
        checkClipboardPermission().then((permission: PermissionType) => {
            if (permission === PermissionType.denied || permission === PermissionType.notSupported) {
                return;
            }
            readImageFromClipboard().then((screenshot: string) => {
                onSave(screenshot);
                setTakingScreenshot(false);
            });
        });
    };
    const keyDownAction = (e: KeyboardEvent): void => {
        if (e.key === "s" || e.key === "S") {
            takeScreenshot();
        }
        if (e.key === "v" && (e.ctrlKey || e.metaKey)) {
            pasteScreenshot();
        }
        if (e.key === "Escape") {
            onCancel();
        }
        if (e.key === "Tab") {
            if (e.target === lastFocusableElementScreenshot.current && !e.shiftKey) {
                e.preventDefault();
                firstFocusableElementScreenshot.current?.focus();
            } else if (e.target === firstFocusableElementScreenshot.current && e.shiftKey) {
                lastFocusableElementScreenshot.current?.focus();
            }
        }
    };

    useEffect(() => {
        const focusableElement = Array.from<HTMLElement>(screenshotOuter.current?.querySelectorAll("[tabindex]") ?? []);

        firstFocusableElementScreenshot.current = focusableElement[0];
        lastFocusableElementScreenshot.current = focusableElement[focusableElement.length - 1];

        firstFocusableElementScreenshot.current.focus();
    }, [firstFocusableElementScreenshot, lastFocusableElementScreenshot]);

    useEffect(() => {
        document.addEventListener("keydown", keyDownAction);
        return (): void => {
            document.removeEventListener("keydown", keyDownAction);
        };
    }, []);

    return (
        <div
            ref={screenshotOuter}
            className={`${SCOPE_PREFIX}-toolbar ${SCOPE_PREFIX}-toolbar--screenshot`}
            data-html2canvas-ignore="true"
        >
            <div className={`${SCOPE_PREFIX}-button-group ${SCOPE_PREFIX}-button-group--screenshot-container`}>
                <button
                    tabIndex={0}
                    type="button"
                    data-testid="take-screenshot"
                    onClick={takeScreenshot}
                    title="Click to take the screenshot"
                    className={`btn mx-button btn-primary ${SCOPE_PREFIX}-button`}
                    autoFocus
                >
                    <TakeScreenshotIcon className={`${SCOPE_PREFIX}-button__take-screenshot-icon`} />
                    {takingScreenshot ? "Taking screenshot ..." : translation.Take_screenshot}
                </button>
                <button
                    tabIndex={0}
                    type="button"
                    onClick={onCancel}
                    className={classNames(
                        "btn mx-button btn-secondary",
                        `${SCOPE_PREFIX}-button ${SCOPE_PREFIX}__btn-text`
                    )}
                >
                    {translation.Cancel}
                </button>
            </div>
        </div>
    );
};

export default ScreenshotContainer;
