import { ReactElement, createElement, useEffect, useRef } from "react";

import CloseIcon from "../resources/CloseIcon";
import { SCOPE_PREFIX } from "../config";
import classNames from "classnames";

const componentName = `${SCOPE_PREFIX}-result`;
const resultClass = classNames(componentName, `${componentName}__open`);

const Result = ({
    onCloseResult,
    translation,
    widgetSettings
}: {
    onCloseResult: () => void;
    translation: Record<string, string | undefined>;
    widgetSettings: {
        submitSuccessImgUrl: string;
    };
}): ReactElement => {
    const closeButtonRef = useRef<HTMLButtonElement | null>(null);
    const closeResultRef = useRef<HTMLButtonElement | null>(null);

    const keyDownAction = (e: KeyboardEvent): void => {
        if (e.key === "Enter" || e.key === "Escape") {
            onCloseResult();
        }

        if (e.key === "Tab") {
            if (e.shiftKey) {
                if (e.target === closeButtonRef.current) {
                    e.preventDefault();
                    closeResultRef.current?.focus();
                }
            } else {
                if (e.target === closeResultRef.current) {
                    e.preventDefault();
                    closeButtonRef.current?.focus();
                }
            }
        }
    };

    useEffect(() => {
        document.addEventListener("keydown", keyDownAction);

        return (): void => {
            document.removeEventListener("keydown", keyDownAction);
        };
    }, []);

    return (
        <div className={classNames(resultClass, `${SCOPE_PREFIX}-dialog`)}>
            <button
                ref={closeButtonRef}
                className={`${SCOPE_PREFIX}-close-button`}
                onClick={onCloseResult}
                aria-label="Close button"
            >
                <CloseIcon className={`${SCOPE_PREFIX}-close-button__icon`} />
            </button>
            <span data-testid="mxfeedback-outro-span" className={`${SCOPE_PREFIX}-dialog__title`}>
                {translation.Success_Outro_Title}
            </span>
            <p className="mxfeedback-dialog__body-text">{translation.Success_Outro_Paragraph}</p>
            {widgetSettings.submitSuccessImgUrl && (
                <img
                    src={widgetSettings.submitSuccessImgUrl}
                    alt="thank you image"
                    className={`${componentName}__result-image`}
                ></img>
            )}
            <div className={`${SCOPE_PREFIX}-button-group spacing-outer-top-medium`}>
                <button
                    ref={closeResultRef}
                    className={`btn mx-button btn-primary ${SCOPE_PREFIX}-button`}
                    form="comment"
                    type="button"
                    onClick={onCloseResult}
                    aria-label="Close result button"
                    autoFocus
                >
                    {translation.Close}
                </button>
            </div>
        </div>
    );
};

export default Result;
