import { ReactElement, createElement, useEffect } from "react";

import CloseIcon from "../resources/CloseIcon";
import { SCOPE_PREFIX } from "../config";

const Lightbox = ({
    onCloseLightbox,
    screenshot
}: {
    onCloseLightbox: () => void;
    screenshot: string;
}): ReactElement => {
    const componentName = `${SCOPE_PREFIX}-lightbox`;

    const keyDownAction = (e: KeyboardEvent): void => {
        if (e.key === "Escape") {
            e.stopPropagation();
            onCloseLightbox();
        }
    };

    useEffect(() => {
        document.addEventListener("keydown", keyDownAction);
        return (): void => {
            document.removeEventListener("keydown", keyDownAction);
        };
    }, []);

    return (
        <div className={`${componentName}`}>
            <button
                className={`${SCOPE_PREFIX}-close-button ${SCOPE_PREFIX}-close-button--white`}
                id="mxfeedback_feedback_lightbox_close_button"
                onClick={onCloseLightbox}
                aria-label="Close button"
                autoFocus
            >
                <CloseIcon className={`${SCOPE_PREFIX}-close-button__icon`} />
            </button>
            <img src={screenshot} className={`${componentName}__image`} />
        </div>
    );
};

export default Lightbox;
