import { Fragment, ReactElement, ReactNode, createElement } from "react";

import { AnnotationContainer } from "./annotation/AnnotationContainer";
import Failed from "./Failed";
import FeedbackForm from "./FeedbackForm";
import { FeedbackStageProps } from "../../typings/FeedbackStageProps";
import { FeedbackStageType } from "./FeedbackRoot";
import Result from "./Result";
import ScreenshotContainer from "./ScreenshotContainer";
import { Stage } from "../utils/enums";

const FeedbackStage = ({
    allowScreenshot,
    scrollableAreaSelector,
    foreignObjectRendering,
    userInfoEntityDS,
    displayNameAttribute,
    emailAddressAttribute,
    sprintrapp,
    feedbackServerLocation,
    translation,
    submitSuccessImgUrl,
    submitFailedImgUrl,
    stageName,
    setStage,
    setScreenshot,
    screenshot,
    onCancelAnnotationAndRemoveScreenshot,
    onUpdateScreenshot,
    setShowLightbox,
    showLightbox,
    setError,
    updateFormData,
    formData,
    error
}: FeedbackStageProps): ReactElement => {
    const stageList: Record<FeedbackStageType, ReactNode> = {
        FORM: (
            <FeedbackForm
                widgetSettings={{
                    sprintrapp,
                    feedbackServerLocation,
                    allowScreenshot,
                    userInfoEntityDS,
                    displayNameAttribute,
                    emailAddressAttribute
                }}
                formData={formData}
                screenshot={screenshot}
                translation={translation}
                takeScreenshot={() => setStage(Stage.SCREENSHOT)}
                updateFormData={updateFormData}
                onUpdateScreenshot={onUpdateScreenshot}
                cancel={() => setStage(Stage.NULL)}
                succeeded={() => setStage(Stage.RESULT)}
                failed={(error: string) => {
                    setError(error);
                    setStage(Stage.FAILED);
                }}
                onShowLightbox={() => setShowLightbox(true)}
                showLightbox={showLightbox}
            ></FeedbackForm>
        ),
        SCREENSHOT: (
            <ScreenshotContainer
                onSave={onUpdateScreenshot}
                onCancel={() => setStage(Stage.FORM)}
                onMinimize={() => setStage(Stage.NULL)}
                foreignObjectRendering={foreignObjectRendering}
                scrollableAreaSelector={scrollableAreaSelector}
                translation={translation}
            ></ScreenshotContainer>
        ),
        ANNOTATE: (
            <AnnotationContainer
                screenshot={screenshot}
                onSave={(screenshot: string) => {
                    setScreenshot(screenshot);
                    setStage(Stage.FORM);
                    updateFormData({ ...formData, screenshot, timestamp: new Date().toString() });
                }}
                onCancel={onCancelAnnotationAndRemoveScreenshot}
                translation={translation}
            ></AnnotationContainer>
        ),
        RESULT: (
            <Result
                widgetSettings={{
                    submitSuccessImgUrl
                }}
                onCloseResult={() => setStage(Stage.NULL)}
                translation={translation}
            ></Result>
        ),
        FAILED: (
            <Failed
                widgetSettings={{
                    submitFailedImgUrl
                }}
                onCloseResult={() => setStage(Stage.NULL)}
                translation={translation}
                error={error}
            ></Failed>
        ),
        NULL: <Fragment></Fragment>
    };

    return <Fragment>{stageList[stageName]}</Fragment>;
};

export default FeedbackStage;
