import { ActionResponse, MxContentForm, MxDataActionParams, MxObject, MxSession, MxUI } from "typings/mx";

import { getBaseWebModelerUrl } from "../config";

// Ported from the old version

function action<T>(params: MxDataActionParams): Promise<T> {
    return new Promise<T>((resolve, reject) => {
        window.mx.data.action({
            params,
            callback: response => {
                let outcome: T | ActionResponse = response;
                if (response instanceof Array && response.length) {
                    const mxObj = response[0];
                    outcome = mxObj.getAttributes().reduce(
                        (obj, attr: string) => ({
                            ...obj,
                            [attr]: mxObj.get(attr)
                        }),
                        {}
                    ) as T;
                }
                resolve(outcome as T);
            },
            error: reject
        });
    });
}

export function getData(params: any): Promise<MxObject | null> {
    return new Promise<MxObject>((resolve, reject) => {
        if (!params) {
            return null;
        }
        window.mx.data.get({ ...params, callback: resolve, error: reject });
    });
}

export const mxData = {
    action,
    get: getData
};

const getUserRoleNames = (): string[] => {
    const mxSession = window.mx.session;
    if ("getUserRoleNames" in mxSession && typeof mxSession["getUserRoleNames"] == "function") {
        return ((mxSession as unknown) as MxSession).getUserRoleNames() as string[];
    }
    return [];
};

export const session = {
    getUserRoleNames
};

// mx.ui.getContentForm is an undocumented API that is intentionally remained  for feedback widget
// We use this to get the documentID, so user can go to the page directly in Studio Pro / Studio.

export const getContentForm = (): MxContentForm | undefined => {
    const mxUI = (window.mx.ui as unknown) as MxUI;

    try {
        if (
            typeof mxUI !== "undefined" &&
            typeof mxUI.getContentForm === "function" &&
            typeof mxUI.getContentForm().path !== "undefined"
        ) {
            return mxUI.getContentForm();
        }
        return undefined;
    } catch (e) {
        return undefined;
    }
};

export const getDocumentId = (): string => {
    const form = getContentForm();
    return form ? form.id : "";
};

const getLoginUrl = (): string => {
    const redirectUrl = `${window.location.origin}/openid/login?continuation=${window.location.href}`;

    return `https://login.mendix.com/oidp/login?ret=${redirectUrl}`;
};

/**
 *
 * @param {string} sprintrId
 *
 */
export const getWebModelerUrl = (sprintrId: string): string => {
    const currentUrl = window.location.origin;
    const matches = currentUrl.match(
        // https://designsystem101-accp.mendixcloud.com/
        // https://designsystem101-accp.mendix.com/
        // https://designsystem101-accp.mxapp.com/
        /^https:\/\/[a-zA-Z0-9]+(-(test|accp|sandbox))?(\.home)?(\.(mendixcloud|mendix|mxapp))\.com/
    );

    const currentEnvironment = matches && matches[2]; // accp

    const baseUrl = getBaseWebModelerUrl(currentEnvironment);
    const projectUrl = `${baseUrl}project/${sprintrId}`;

    const form = getContentForm();
    if (!form) {
        return projectUrl;
    }
    const documentId = form.id;

    // NOTE: the trailing slash is important. Studio needs this trailing slash
    return documentId ? `${projectUrl}/document/${documentId}/` : baseUrl;
    /**
     * https://studio.test.mendix.com/project/3c69f543-ed2f-4545-8c30-1a98d1aa9747/document/7b22940f-b2dd-43e1-9a0b-25011a7e1b33
     */
};

export const getAppUrl = (): string => {
    const hasAppUrl = window.mx && window.mx.appUrl;
    const isOffline = hasAppUrl && window.mx.isOffline;
    const getOfflineUrl = (): string =>
        JSON.stringify({
            url: window.mx.appUrl,
            offline: window.mx.isOffline()
        });
    const getOnlineUrl = (): string => (hasAppUrl ? window.mx.appUrl : window.location.origin);

    return isOffline ? getOfflineUrl() : getOnlineUrl();
};

export const redirectToLogin = (): void => {
    window.mx.logout(); // not async
    window.location.href = getLoginUrl();
};
