    tabIndex,
    allowScreenshot,
    scrollableAreaSelector,
    foreignObjectRendering,
    sprintrapp,
    submitSuccessImgUrl,
    submitFailedImgUrl,
    userInfoEntityDS,
    displayNameAttribute,
