import { imageToBase64 } from "../utils/screenshot/imageUtils";

export const useUploadScreenshot = (
    validateImage: (curFiles: FileList | null) => File | undefined,
    setUploadFileValid: (value: React.SetStateAction<boolean>) => void,
    updateScreenshot: (screenshot: string) => void
): ((e: React.ChangeEvent<HTMLInputElement>) => void) => {
    return (e: React.ChangeEvent<HTMLInputElement>): void => {
        const image = validateImage(e.target.files);
        if (!image) {
            setUploadFileValid(false);
            return;
        }
        setUploadFileValid(true);
        imageToBase64(image)
            .then(updateScreenshot)
            .catch((error: Error) => console.error(error));
    };
};
