import { getContentForm } from "./mxHelpers";
import { version } from "../../package.json";

interface Payload {
    title: string;
    description: string;
    issueType: "Issue";
    submitter: {
        userId?: string;
        email: string;
        displayName: string;
    };
    metadata: {
        userRoles: string;
        location: string;
        form: string;
        userAgent: string;
        screenWidth: number;
        screenHeight: number;
    };
    imageAttachment: string;
}

const callFeedbackAPI = ({
    sprintrapp,
    feedbackServerLocation,
    screenshot,
    userId,
    username,
    email,
    userRoles,
    title,
    description
}: {
    sprintrapp: string;
    feedbackServerLocation: string;
    screenshot: string | null;
    userId?: string;
    username: string;
    email: string;
    userRoles: string[];
    title: string;
    description: string;
}): Promise<Response> => {
    const img = screenshot ? screenshot.replace(/(data:image\/png;base64,)/, "") : "";

    const form = getContentForm();

    const data: Payload = {
        title,
        description,
        issueType: "Issue",
        submitter: {
            userId,
            email,
            displayName: username ? username : email
        },
        metadata: {
            userRoles: userRoles.join(","),
            location: window.location.href,
            form: form ? form.path : "",
            userAgent: navigator.userAgent,
            screenWidth: window.screen.width,
            screenHeight: window.screen.height
        },
        imageAttachment: img
    };

    if (!data.submitter.userId) {
        if (!data.submitter.displayName) {
            console.warn("Feedback Widget >> Username is empty");
            return Promise.reject(new Error("username is empty."));
        }

        if (!data.submitter.email) {
            console.warn("Feedback Widget >> Email address is empty");
            return Promise.reject(new Error("email address is empty."));
        }
    }

    return window.fetch(`${feedbackServerLocation}rest/v3/feedbackapi/projects/${sprintrapp}/issues`, {
        method: "POST",
        body: JSON.stringify(data),
        headers: {
            "Content-Type": "application/json; charset=utf-8",
            ClientIdentifier: `Feedback Widget v${version}`
        }
    });
};

export { callFeedbackAPI };
