import { ReactElement, createElement } from "react";

import { SCOPE_PREFIX } from "../config";
import TooltipIcon from "../resources/TooltipIcon";

const Tooltip = ({ tip }: { tip: string }): ReactElement => {
    const componentName = `${SCOPE_PREFIX}-tooltip`;

    return (
        <div className={`${componentName}`}>
            <TooltipIcon className={`${componentName}__icon`} />
            <p className={`${componentName}__tip`}>{tip}</p>
        </div>
    );
};

export default Tooltip;
