import { UserInfo } from "typings/UserInfo";
import { WidgetSettings } from "typings/WidgetSettings";
import { session } from "../utils/mxHelpers";
import { useEffect } from "react";

export const useDetermineUserType = (
    setShowEmail: (value: React.SetStateAction<boolean>) => void,
    widgetSettings: WidgetSettings,
    setUserData: (value: React.SetStateAction<UserInfo>) => void
): void => {
    useEffect(() => {
        if (
            !widgetSettings.userInfoEntityDS ||
            !widgetSettings.userInfoEntityDS.items ||
            widgetSettings.userInfoEntityDS.items.length < 1
        ) {
            setShowEmail(true);
        } else if (widgetSettings.emailAddressAttribute) {
            const emailAddress =
                widgetSettings.emailAddressAttribute(widgetSettings.userInfoEntityDS.items[0]).value || "";
            const displayName = widgetSettings.displayNameAttribute
                ? widgetSettings.displayNameAttribute(widgetSettings.userInfoEntityDS.items[0]).value || ""
                : emailAddress;
            const userRoles = session.getUserRoleNames();

            setUserData({ name: displayName, email: emailAddress, userRoles: userRoles });

            // Later need to check whether email address is an empty string and toggle showEmail to true.
            setShowEmail(false);
        }
    }, [widgetSettings]);
};
