import { CursorPosition, ImageSize } from "typings/annotation";
import React, { ReactElement, createElement, useEffect, useRef } from "react";

import { SCOPE_PREFIX } from "../../config";

const componentName = `${SCOPE_PREFIX}-annotation-canvas`;

export const AnnotationCanvas = ({
    screenshot,
    imageSize,
    setImageSize,
    setCursorPosition,
    imageCanvasRef,
    freehandCanvasRef
}: {
    screenshot: string;
    imageSize: ImageSize | undefined;
    setImageSize: (value: ImageSize) => void;
    setCursorPosition: (value: CursorPosition) => void;
    imageCanvasRef: React.RefObject<HTMLCanvasElement>;
    freehandCanvasRef: React.RefObject<HTMLCanvasElement>;
}): ReactElement => {
    const imageRef = useRef<HTMLImageElement>(null);

    const updateCursorPosition = (): void => {
        if (!imageCanvasRef.current) {
            return;
        }
        // For top and left position check canvas position
        setCursorPosition({
            top: imageCanvasRef.current.offsetTop,
            left: imageCanvasRef.current.offsetLeft
        });
    };

    useEffect(() => {
        if (imageRef.current && imageCanvasRef.current && imageSize) {
            const context = imageCanvasRef.current.getContext("2d");
            if (!context) {
                return;
            }
            context.drawImage(imageRef.current, 0, 0, imageSize.width, imageSize.height);
            updateCursorPosition();
        }
    }, [imageSize]);

    const updateImageSize = (): void => {
        if (!imageRef.current) {
            return;
        }
        // If image is small we don't need to scale it down for annotation
        // For width and height check actual image size
        const resize = imageRef.current.width > window.innerWidth * 0.8;
        if (resize) {
            const ratio = (window.innerWidth * 0.8) / imageRef.current.width;
            setImageSize({
                width: imageRef.current.width * ratio,
                height: imageRef.current.height * ratio
            });
        } else {
            setImageSize({
                width: imageRef.current.width,
                height: imageRef.current.height
            });
        }
    };

    useEffect(() => {
        if (!imageRef.current || !imageCanvasRef.current) {
            return;
        }

        updateCursorPosition();
        imageCanvasRef.current.addEventListener("resize", updateCursorPosition);

        return () => {
            if (imageCanvasRef.current) {
                imageCanvasRef.current.removeEventListener("resize", updateCursorPosition);
            }
        };
    }, [imageRef.current, imageCanvasRef.current]);

    useEffect(() => {
        window.addEventListener("resize", updateImageSize);
        return (): void => {
            window.removeEventListener("resize", updateImageSize);
        };
    }, []);

    return (
        <div className={componentName}>
            <img ref={imageRef} src={screenshot} className="hidden" onLoad={updateImageSize} />
            <canvas
                className={`${componentName}__canvas`}
                ref={imageCanvasRef}
                width={imageSize ? imageSize.width : undefined}
                height={imageSize ? imageSize.height : undefined}
            ></canvas>
            <canvas
                className={`${componentName}__canvas`}
                ref={freehandCanvasRef}
                width={imageSize ? imageSize.width : undefined}
                height={imageSize ? imageSize.height : undefined}
            ></canvas>
        </div>
    );
};
