import { FEEDBACK_COMMENT_MAX_LENGTH, FEEDBACK_TITLE_MAX_LENGTH } from "../config";

const validateFeedbackComment = (description: string): boolean => {
    if (!description) {
        // Description is not required
        return true;
    }

    if (description.length > FEEDBACK_COMMENT_MAX_LENGTH) {
        return false;
    }

    return true;
};

const validateFeedbackTitle = (title: string): boolean => {
    if (!title || !title.trim()) {
        return false;
    }

    if (title.length > FEEDBACK_TITLE_MAX_LENGTH) {
        return false;
    }

    return true;
};

const validateEmail = (email: string): boolean => {
    if (!email) {
        return false;
    }
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email.toLowerCase());
};

const validateImage = (curFiles: FileList | null): File | undefined => {
    if (!curFiles || curFiles.length === 0) {
        console.error("Feedback Widget >> No file selected");
        return;
    }

    const fileType = curFiles[0].type.split("/");
    if (fileType[0] !== "image") {
        console.error("Feedback Widget >> File is not an image");
        return;
    }

    if (curFiles[0].size > 5 * 1024 * 1024) {
        console.error("Feedback Widget >> Image is too big");
        return;
    }

    return curFiles[0];
};

export { validateFeedbackComment, validateFeedbackTitle, validateEmail, validateImage };
