import { ReactElement, createElement, forwardRef, useRef } from "react";

import { SCOPE_PREFIX } from "../config";
import TakeScreenshotIcon from "../resources/TakeScreenshotIcon";
import UploadScreenshotIcon from "../resources/UploadScreenshotIcon";
import classNames from "classnames";

interface ScreenshotButtonGroupProps {
    takeScreenshot: () => void;
    translation: Record<string, string | undefined>;
    uploadScreenshot: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

const ScreenshotButtonGroup = forwardRef(
    (props: ScreenshotButtonGroupProps, ref: React.RefObject<HTMLButtonElement>): ReactElement => {
        const { takeScreenshot, translation, uploadScreenshot } = props;
        const uploaderInput = useRef<HTMLInputElement>(null);
        const uploaderInputButton = useRef<HTMLButtonElement>(null);

        const feedbackFormComponentName = `${SCOPE_PREFIX}-feedback-form`;

        return (
            <div
                className={classNames(
                    `${SCOPE_PREFIX}-button-group ${SCOPE_PREFIX}-screenshot-button-group`,
                    `${SCOPE_PREFIX}-button-group--justify-center ${SCOPE_PREFIX}-button-group--row-xs`
                )}
            >
                <button
                    className={classNames(
                        "btn mx-button btn-primary mxfeedback-button",
                        `${SCOPE_PREFIX}-button`,
                        `${feedbackFormComponentName}__screenshot-button`
                    )}
                    id="mxfeedback_feedback_take_screenshot_button"
                    ref={ref}
                    type="button"
                    onClick={takeScreenshot}
                    data-testid="enter-screenshot-mode"
                    tabIndex={0}
                >
                    <TakeScreenshotIcon />
                    {translation.Enter_screenshot_mode}
                </button>
                <input
                    ref={uploaderInput}
                    type="file"
                    onChange={uploadScreenshot}
                    accept=".gif,.jpg,.jpeg,.png"
                    className="hidden"
                />
                <button
                    tabIndex={0}
                    className={classNames(
                        "btn mx-button",
                        `${SCOPE_PREFIX}-button`,
                        `${feedbackFormComponentName}__upload-button`
                    )}
                    id="mxfeedback_feedback_upload_screenshot_button"
                    ref={uploaderInputButton}
                    onClick={(): void => uploaderInput && uploaderInput.current?.click()}
                    name="feedback_attachment"
                    data-testid="upload-from-computer"
                >
                    <UploadScreenshotIcon />
                    {translation.Upload_from_computer}
                </button>
            </div>
        );
    }
);

export default ScreenshotButtonGroup;
