import { validateEmail, validateFeedbackComment, validateFeedbackTitle } from "../utils/validation";

import { FormData } from "typings/FormData";
import { UserInfo } from "typings/UserInfo";
import { WidgetSettings } from "typings/WidgetSettings";
import { callFeedbackAPI } from "../utils/feedbackAPI";

export const useSubmitFeedback = (
    setFeedbackCommentValid: React.Dispatch<React.SetStateAction<boolean>>,
    setFeedbackTitleValid: React.Dispatch<React.SetStateAction<boolean>>,
    setFeedbackEmailValid: React.Dispatch<React.SetStateAction<boolean>>,
    showEmail: boolean,
    setShowEmail: React.Dispatch<React.SetStateAction<boolean>>,
    titleInput: React.RefObject<HTMLInputElement>,
    descriptionInput: React.RefObject<HTMLTextAreaElement>,
    emailInput: React.RefObject<HTMLInputElement>,
    setSubmitInProgress: React.Dispatch<React.SetStateAction<boolean>>,
    widgetSettings: WidgetSettings,
    screenshot: string,
    updateFormData: (formData: FormData) => void,
    updateScreenshot: (screenshot: string) => void,
    succeeded: () => void,
    failed: (error: string) => void
): (({ title, description, userData }: { title: string; description: string; userData: UserInfo }) => void) => {
    return ({ title, description, userData }: { title: string; description: string; userData: UserInfo }): void => {
        const { sprintrapp, feedbackServerLocation } = widgetSettings;

        const commentValid = validateFeedbackComment(description);
        setFeedbackCommentValid(commentValid);

        const titleValid = validateFeedbackTitle(title);
        setFeedbackTitleValid(titleValid);

        const emailValid = validateEmail(userData.email);
        setFeedbackEmailValid(emailValid);

        if (!emailValid && !showEmail) {
            setShowEmail(true);
        }

        if (!titleValid && titleInput.current) {
            titleInput.current.focus();
            return;
        }

        if (!commentValid && descriptionInput.current) {
            descriptionInput.current.focus();
            return;
        }

        if (!emailValid && emailInput.current) {
            emailInput.current.focus();
            return;
        }

        setSubmitInProgress(true);

        callFeedbackAPI({
            sprintrapp,
            feedbackServerLocation,
            screenshot: widgetSettings.allowScreenshot && screenshot ? screenshot : null,
            username: userData ? userData.name : "",
            email: userData ? userData.email : "",
            userRoles: userData ? userData.userRoles : [],
            title,
            description
        })
            .then((response: Response) => {
                if (response.ok) {
                    updateFormData({ title: "", description: "", email: "", screenshot: "", timestamp: "" });
                    updateScreenshot("");
                    succeeded();
                } else {
                    console.warn(
                        `Feedback Widget >> Failed submitting feedback with the following error: ${response.statusText}`
                    );
                    failed(response.statusText);
                }
                setSubmitInProgress(false);
            })
            .catch(error => {
                console.warn(`Feedback Widget >> Failed submitting feedback with the following error: ${error}`);
                setSubmitInProgress(false);
                failed(error);
            });
    };
};
