/**
 *
 * @param {string} selector
 * 1. user provided selector
 * 2. Atlas default scrollable area selector
 */
export const getScroll = (selector: string): { x: number; y: number } => {
    const scrollableElement =
        (!!selector && document.querySelector<HTMLElement>(selector)) ||
        document.documentElement ||
        document.body.parentNode ||
        document.body;

    const x = scrollableElement
        ? scrollableElement.scrollLeft
        : window.scrollX ||
          window.pageXOffset ||
          document.body.scrollLeft + ((document.documentElement && document.documentElement.scrollLeft) || 0);
    const y = scrollableElement
        ? scrollableElement.scrollTop
        : window.scrollY ||
          window.pageYOffset ||
          document.body.scrollTop + ((document.documentElement && document.documentElement.scrollTop) || 0);

    return { x, y };
};

const getCursorPosition = (event: MouseEvent): { dotX: number; dotY: number } => {
    const dotX = Math.round(event.clientX);
    const dotY = Math.round(event.clientY);
    return { dotX, dotY };
};

export default getCursorPosition;
