import { UserInfo } from "typings/UserInfo";
import { FormData } from "typings/FormData";
import { ChangeEventHandler } from "react";
import { validateEmail } from "../utils/validation";

export const useHandleUserEmailInfo = (
    setFeedbackEmailValid: (value: React.SetStateAction<boolean>) => void,
    setUserData: (value: React.SetStateAction<UserInfo>) => void,
    userData: UserInfo,
    updateFormData: (formData: FormData) => void,
    formData: FormData
): ChangeEventHandler<HTMLInputElement> => {
    const handleUserEmailInfo = (e: { target: { value: string } }): void => {
        const value = e.target.value.trim();
        const emailValid = validateEmail(value);

        if (emailValid) {
            setFeedbackEmailValid(emailValid);
        }

        setUserData({ ...userData, email: value });
        updateFormData({ ...formData, email: value, timestamp: new Date().toString() });
    };

    return handleUserEmailInfo;
};
