import { ReactElement, createElement, useEffect, useRef } from "react";

import CloseIcon from "../resources/CloseIcon";
import { SCOPE_PREFIX } from "../config";
import classNames from "classnames";

const componentName = `${SCOPE_PREFIX}-failed`;
const failedClass = classNames(componentName, `${componentName}__open`);

const Failed = ({
    error,
    onCloseResult,
    translation,
    widgetSettings
}: {
    error: string;
    onCloseResult: () => void;
    translation: Record<string, string | undefined>;
    widgetSettings: {
        submitFailedImgUrl: string;
    };
}): ReactElement => {
    const closeButtonRef = useRef<HTMLButtonElement | null>(null);
    const closeResultRef = useRef<HTMLButtonElement | null>(null);
    console.error(`Feedback Widget >> ${error}`);

    const keyDownAction = (e: KeyboardEvent): void => {
        if (e.key === "Enter" || e.key === "Escape") {
            onCloseResult();
        }

        if (e.key === "Tab") {
            if (e.shiftKey) {
                if (e.target === closeButtonRef.current) {
                    e.preventDefault();
                    closeResultRef.current?.focus();
                }
            } else {
                if (e.target === closeResultRef.current) {
                    e.preventDefault();
                    closeButtonRef.current?.focus();
                }
            }
        }
    };

    useEffect(() => {
        document.addEventListener("keydown", keyDownAction);

        return (): void => {
            document.removeEventListener("keydown", keyDownAction);
        };
    }, []);

    return (
        <div className={classNames(failedClass, `${SCOPE_PREFIX}-dialog`)}>
            <button
                ref={closeButtonRef}
                className={`${SCOPE_PREFIX}-close-button`}
                onClick={onCloseResult}
                aria-label="Close button"
            >
                <CloseIcon className={`${SCOPE_PREFIX}-close-button__icon`} />
            </button>
            <span className={`${SCOPE_PREFIX}-dialog__title`}>{translation.Submit_Feedback_Error_Title}</span>
            <span className={`${componentName}__message`}>{translation.Submit_Feedback_Error_Message}</span>
            {widgetSettings.submitFailedImgUrl && (
                <img
                    src={widgetSettings.submitFailedImgUrl}
                    alt="failed image"
                    className={`${componentName}__error-image`}
                ></img>
            )}
            <div className={`${SCOPE_PREFIX}-button-group spacing-outer-top-medium`}>
                <button
                    ref={closeResultRef}
                    className={classNames("btn btn-primary", `${componentName}__close-button ${SCOPE_PREFIX}-button`)}
                    form="comment"
                    type="button"
                    onClick={onCloseResult}
                    aria-label="Close failed button"
                    autoFocus
                >
                    {translation.Close}
                </button>
            </div>
        </div>
    );
};

export default Failed;
