import { Dispatch, SetStateAction, useEffect, useState } from "react";
import { FORMDATA_DELETE_INTERVAL, LOCAL_STORAGE_FORM_DATA_KEY } from "../config";
import { getFromLocalStorage, saveToLocalStorage } from "../utils/localstorage";

import { FormData } from "typings/FormData";

export const useFormData = (sprintrapp: string): [FormData, Dispatch<SetStateAction<FormData>>] => {
    //Use the local storage data
    const existingFormData = getFromLocalStorage(`${LOCAL_STORAGE_FORM_DATA_KEY}__${sprintrapp}`);
    const emptyFormData = {
        title: "",
        description: "",
        email: "",
        screenshot: "",
        timestamp: ""
    };

    const [formData, setFormData] = useState(existingFormData ? JSON.parse(existingFormData) : emptyFormData);

    const shouldClearStoredFormData = (): boolean => {
        if (!formData.timestamp) {
            return false;
        }

        const now = new Date();
        const timestamp = new Date(formData.timestamp.toString()).getTime();
        const diffInSeconds = Math.round((now.getTime() - timestamp) / 1000);

        return diffInSeconds >= FORMDATA_DELETE_INTERVAL;
    };

    useEffect(() => {
        if (existingFormData && JSON.parse(existingFormData).timestamp && shouldClearStoredFormData()) {
            setFormData(emptyFormData);
        }
    }, [existingFormData, formData, setFormData, shouldClearStoredFormData]);

    useEffect(() => {
        saveToLocalStorage(`${LOCAL_STORAGE_FORM_DATA_KEY}__${sprintrapp}`, formData);
    }, [formData]);

    return [formData, setFormData];
};
