interface ClipboardItem {
    types: string[];
    getType(type: string): Promise<Blob>;
}

const imageToBase64 = (blob: File | Blob): Promise<string> => {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(blob);
        reader.onloadend = () => {
            if (reader.result && typeof reader.result === "string") {
                resolve(reader.result);
            } else {
                reject(new Error("Error reading file"));
            }
        };
    });
};

const isClipboardAvailable = (): boolean => {
    if (window.navigator.clipboard && (window.navigator.clipboard as any).read) {
        return true;
    }
    return false;
};

export enum PermissionType {
    granted,
    prompt,
    denied,
    notSupported
}

const checkClipboardPermission = (): Promise<PermissionType> => {
    return new Promise((resolve, reject) => {
        if (!isClipboardAvailable()) {
            reject(PermissionType.notSupported);
        } else {
            const permit = ({ name: "clipboard-read" } as unknown) as PermissionDescriptor;
            navigator.permissions.query(permit).then((result: PermissionStatus) => {
                switch (result.state) {
                    case "granted":
                        resolve(PermissionType.granted);
                        break;
                    case "prompt":
                        reject(PermissionType.prompt);
                        break;
                    case "denied":
                        reject(PermissionType.denied);
                        break;
                    default:
                        reject(PermissionType.notSupported);
                }
            });
        }
    });
};

const readImageFromClipboard = (): Promise<string> => {
    return new Promise((resolve, reject) => {
        try {
            (window.navigator.clipboard as any).read().then((clipboardItems: ClipboardItem[]) => {
                for (const clipboardItem of clipboardItems) {
                    for (const type of clipboardItem.types) {
                        if (type !== "image/png") {
                            continue;
                        }
                        clipboardItem
                            .getType(type)
                            .then((blob: Blob) => imageToBase64(blob).then(image => resolve(image)));
                    }
                }
            });
        } catch (err) {
            reject(err);
        }
    });
};

export { imageToBase64, readImageFromClipboard, isClipboardAvailable, checkClipboardPermission };
