import { Color, Thickness, ToolSet } from "../../../typings/annotation";
import { ReactElement, createElement, useEffect, useRef, useState } from "react";

import ArrowDownIcon from "../../resources/ArrowDownIcon";
import CheckIcon from "../../resources/CheckIcon";
import ClearIcon from "../../resources/ClearIcon";
import FreehandIcon from "../../resources/FreehandIcon";
import { SCOPE_PREFIX } from "../../config";
import classNames from "classnames";

const toolButtonComponentName = `${SCOPE_PREFIX}-tool-button`;
const componentName = `${SCOPE_PREFIX}-toolbar`;

const colors = [
    { name: "midnight", color: Color.MidnightBlue },
    { name: "grey", color: Color.Grey },
    { name: "green", color: Color.Green },
    { name: "blue", color: Color.Blue },
    { name: "purple", color: Color.Purple },
    { name: "magenta", color: Color.Magenta },
    { name: "red", color: Color.Red },
    { name: "yellow", color: Color.Yellow }
];

const thickness = [
    { name: "one", width: Thickness.One },
    { name: "two", width: Thickness.Two },
    { name: "three", width: Thickness.Three },
    { name: "four", width: Thickness.Four },
    { name: "five", width: Thickness.Five },
    { name: "six", width: Thickness.Six }
];

const ColorButton = ({
    colorName,
    colorHex,
    setMethod,
    selected,
    freehandToolRef,
    tabIndex
}: {
    colorName: string;
    colorHex: Color;
    setMethod: (colorName: Color) => void;
    selected: boolean;
    freehandToolRef: React.RefObject<HTMLButtonElement>;
    tabIndex: number;
}): ReactElement => {
    const onSetColor = (): void => {
        setMethod(colorHex);
        freehandToolRef.current?.focus();
    };

    return (
        <button
            className={classNames(
                `${toolButtonComponentName}__color`,
                `${toolButtonComponentName}__color--${colorName}`
            )}
            onClick={onSetColor}
            tabIndex={tabIndex}
        >
            {selected && <CheckIcon />}
        </button>
    );
};

const ThicknessButton = ({
    thicknessName,
    width,
    setMethod,
    selected,
    freehandToolRef,
    tabIndex
}: {
    thicknessName: string;
    width: Thickness;
    setMethod: (width: Thickness) => void;
    selected: boolean;
    freehandToolRef: React.RefObject<HTMLButtonElement>;
    tabIndex: number;
}): ReactElement => {
    const onSetThickness = (): void => {
        setMethod(width);
        freehandToolRef.current?.focus();
    };

    return (
        <button
            className={classNames(
                `${SCOPE_PREFIX}-button`,
                `${toolButtonComponentName}__thickness`,
                `${toolButtonComponentName}__thickness--${thicknessName}`,
                selected ? `${toolButtonComponentName}__thickness--selected` : ""
            )}
            onClick={onSetThickness}
            tabIndex={tabIndex}
        ></button>
    );
};

export const AnnotationToolbar = ({
    menuRef,
    freehandToolRef,
    currentTool,
    setFreehandTool,
    currentColor,
    setCurrentColor,
    currentThickness,
    setCurrentThickness,
    onClearClicked,
    onSaveClicked,
    onCancel,
    translation
}: {
    menuRef: React.RefObject<HTMLDivElement>;
    freehandToolRef: React.RefObject<HTMLButtonElement>;
    currentTool: ToolSet;
    setFreehandTool: () => void;
    currentColor: Color;
    setCurrentColor: React.Dispatch<React.SetStateAction<Color>>;
    currentThickness: Thickness;
    setCurrentThickness: React.Dispatch<React.SetStateAction<Thickness>>;
    onClearClicked: () => void;
    onSaveClicked: () => void;
    onCancel: () => void;
    translation: Record<string, string | undefined>;
}): ReactElement => {
    const [showDropdown, setShowDropdown] = useState(false);
    const ref = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const onOutsideClick = (event: Event & { target: Element }): void => {
            if (showDropdown && ref.current && !ref.current.contains(event.target)) {
                setShowDropdown(false);
            }
        };

        document.addEventListener("mousedown", onOutsideClick);

        return () => {
            document.removeEventListener("mousedown", onOutsideClick);
        };
    }, [showDropdown]);

    return (
        <div
            className={`${SCOPE_PREFIX}-toolbar ${SCOPE_PREFIX}-toolbar--gap-lg ${SCOPE_PREFIX}-toolbar--annotation`}
            data-html2canvas-ignore="true"
            ref={menuRef}
        >
            <div className={`${SCOPE_PREFIX}-button-group ${SCOPE_PREFIX}-button-group--gap-md`}>
                <button
                    className={classNames(
                        `${SCOPE_PREFIX}-tool-button`,
                        currentTool === ToolSet.Freehand && `${SCOPE_PREFIX}-tool-button--active`
                    )}
                    onClick={(): void => {
                        setShowDropdown(!showDropdown);
                        if (currentTool === ToolSet.Freehand) {
                            return;
                        }
                        setFreehandTool();
                    }}
                    ref={freehandToolRef}
                    autoFocus
                    tabIndex={0}
                >
                    <div className={`${SCOPE_PREFIX}-tool-button__inner`}>
                        <FreehandIcon className={`${SCOPE_PREFIX}-tool-button__icon`} />
                        <span className={`${SCOPE_PREFIX}-tool-button__label`}>{translation.Annotate}</span>
                    </div>
                    <div className={`${SCOPE_PREFIX}-tool-button__dropdown`}>
                        <ArrowDownIcon className={`${SCOPE_PREFIX}-tool-button__caret`} />
                        {showDropdown && (
                            <div ref={ref} className={`${SCOPE_PREFIX}-tool-button__menu`}>
                                <div className={`${SCOPE_PREFIX}-tool-button__list`}>
                                    {colors.map(item => (
                                        <ColorButton
                                            key={item.name}
                                            colorName={item.name}
                                            colorHex={item.color}
                                            setMethod={setCurrentColor}
                                            selected={currentColor === item.color}
                                            freehandToolRef={freehandToolRef}
                                            tabIndex={0}
                                        />
                                    ))}
                                </div>
                                <div
                                    className={`${SCOPE_PREFIX}-tool-button__list ${SCOPE_PREFIX}-tool-button__list--col-3`}
                                >
                                    {thickness.map(item => (
                                        <ThicknessButton
                                            key={item.name}
                                            thicknessName={item.name}
                                            setMethod={setCurrentThickness}
                                            width={item.width}
                                            selected={currentThickness === item.width}
                                            freehandToolRef={freehandToolRef}
                                            tabIndex={0}
                                        />
                                    ))}
                                </div>
                            </div>
                        )}
                    </div>
                </button>
                <button className={`${SCOPE_PREFIX}-tool-button`} onClick={onClearClicked} tabIndex={0}>
                    <div className={`${SCOPE_PREFIX}-tool-button__inner`}>
                        <ClearIcon className={`${SCOPE_PREFIX}-tool-button__icon`} />
                        <span className={`${SCOPE_PREFIX}-tool-button__label`}>{translation.Clear}</span>
                    </div>
                </button>
            </div>
            <div className={`${SCOPE_PREFIX}-button-group`}>
                <button
                    className={classNames(
                        "btn mx-button btn-secondary",
                        `${SCOPE_PREFIX}-button ${SCOPE_PREFIX}__btn-text`,
                        `${componentName}__cancel-screenshot-button`
                    )}
                    type="button"
                    onClick={onCancel}
                    data-testid="cancel-screenshot"
                    tabIndex={0}
                >
                    {translation.Cancel}
                </button>
                <button
                    className={classNames(
                        "btn mx-button btn-primary",
                        `${componentName}__save-screenshot-button ${SCOPE_PREFIX}-button`
                    )}
                    type="button"
                    onClick={onSaveClicked}
                    data-testid="save-screenshot"
                    tabIndex={0}
                >
                    {translation.Done}
                </button>
            </div>
        </div>
    );
};
