export const useSetCursorFocus = (
    title: string,
    titleInput: React.RefObject<HTMLInputElement>,
    description: string,
    descriptionInput: React.RefObject<HTMLTextAreaElement>,
    screenshot: string,
    screenshotButton: React.RefObject<HTMLButtonElement>,
    emailInput: React.RefObject<HTMLInputElement>,
    email: string,
    feedbackEmailValid: boolean
): (() => void) => {
    const setCursorFocus = (): void => {
        if (!title && titleInput.current) {
            titleInput.current.focus();
            return;
        }
        if (!description && descriptionInput.current) {
            descriptionInput.current.focus();
            return;
        }
        if (!screenshot && screenshotButton.current) {
            screenshotButton.current.focus();
            return;
        }
        if (emailInput.current && (!email || !feedbackEmailValid)) {
            emailInput.current.focus();
            return;
        }
    };

    return setCursorFocus;
};
