import { ReactElement, createElement } from "react";
import classNames from "classnames";
import { SCOPE_PREFIX } from "../config";

const StartButton = ({
    onClick,
    className,
    tabIndex,
    translation
}: {
    className?: string;
    tabIndex?: number;
    onClick: () => void;
    translation: Record<string, string | undefined>;
}): ReactElement => {
    return (
        <button
            data-html2canvas-ignore="true"
            data-testid="feedback"
            type="button"
            onClick={onClick}
            className={classNames(`${SCOPE_PREFIX}-button`, className)}
            tabIndex={tabIndex}
        >
            {translation.Feedback}
        </button>
    );
};

export default StartButton;
