import { Color, CursorPosition, ImageSize, Thickness, ToolSet } from "../../../typings/annotation";
import { ReactElement, createElement, useEffect, useRef, useState } from "react";

import { AnnotationCanvas } from "./AnnotationCanvas";
import { AnnotationToolbar } from "./AnnotationToolbar";
import { SCOPE_PREFIX } from "../../config";
import { bindFreehand } from "../../utils/annotation/bindFreehand";

const componentName = `${SCOPE_PREFIX}-annotation-frame`;

export const AnnotationContainer = ({
    screenshot,
    onSave,
    onCancel,
    translation
}: {
    screenshot: string;
    onCancel: () => void;
    onSave: (annotate: string) => void;
    translation: Record<string, string | undefined>;
}): ReactElement => {
    const menuRef = useRef<HTMLDivElement>(null);
    const freehandToolRef = useRef<HTMLButtonElement>(null);
    const freehandCanvasRef = useRef<HTMLCanvasElement>(null);
    const imageCanvasRef = useRef<HTMLCanvasElement>(null);
    const [imageSize, setImageSize] = useState<ImageSize | undefined>();
    const [cursorPosition, setCursorPosition] = useState<CursorPosition | undefined>();
    const [currentTool, setCurrentTool] = useState<ToolSet>(ToolSet.Freehand);
    const [currentColor, setCurrentColor] = useState<Color>(Color.Yellow);
    const [currentThickness, setCurrentThickness] = useState<Thickness>(Thickness.Three);
    const annotationOuter = useRef<HTMLDivElement | null>(null);
    const firstFocusableElementAnnotation = useRef<HTMLElement | null>(null);
    const lastFocusableElementAnnotation = useRef<HTMLElement | null>(null);
    let detachBinder: undefined | (() => void);

    useEffect(() => {
        if (freehandCanvasRef.current && cursorPosition) {
            if (detachBinder) {
                detachBinder();
            }
            detachBinder = bindFreehand(freehandCanvasRef.current, cursorPosition, currentThickness, currentColor);
        }
        return () => {
            if (detachBinder) {
                detachBinder();
            }
        };
    }, [currentColor, currentThickness, cursorPosition]);

    const setFreehandTool = (): void => {
        setCurrentTool(ToolSet.Freehand);
        if (freehandCanvasRef.current) {
            bindFreehand(freehandCanvasRef.current, cursorPosition, currentThickness, currentColor);
        }
    };

    const onSaveClicked = (): void => {
        if (!imageCanvasRef.current) {
            return;
        }
        const context = imageCanvasRef.current.getContext("2d");
        if (!context) {
            return;
        }
        if (freehandCanvasRef.current) {
            context.drawImage(freehandCanvasRef.current, 0, 0);
        }
        onSave(imageCanvasRef.current.toDataURL());
    };

    const onClearClicked = (): void => {
        if (!freehandCanvasRef.current) {
            return;
        }

        const freehandContext = freehandCanvasRef.current.getContext("2d");

        if (!freehandContext) {
            return;
        }

        freehandContext.clearRect(0, 0, freehandCanvasRef.current.width, freehandCanvasRef.current.height);
    };
    const keyDownAction = (e: KeyboardEvent): void => {
        if (e.key === "Escape") {
            onCancel();
        }

        if (e.key === "Tab") {
            if (e.target === lastFocusableElementAnnotation.current && !e.shiftKey) {
                e.preventDefault();
                firstFocusableElementAnnotation.current?.focus();
            } else if (e.target === firstFocusableElementAnnotation.current && e.shiftKey) {
                lastFocusableElementAnnotation.current?.focus();
            }
        }
    };

    useEffect(() => {
        const focusableElement = Array.from<HTMLElement>(annotationOuter.current?.querySelectorAll("[tabindex]") ?? []);

        firstFocusableElementAnnotation.current = focusableElement[0];
        lastFocusableElementAnnotation.current = focusableElement[focusableElement.length - 1];

        firstFocusableElementAnnotation.current.focus();
    }, [firstFocusableElementAnnotation, lastFocusableElementAnnotation]);

    useEffect(() => {
        document.addEventListener("keydown", keyDownAction);
        return (): void => {
            document.removeEventListener("keydown", keyDownAction);
        };
    }, []);

    return (
        <div ref={annotationOuter} className={componentName}>
            <AnnotationToolbar
                menuRef={menuRef}
                freehandToolRef={freehandToolRef}
                currentTool={currentTool}
                setFreehandTool={setFreehandTool}
                currentColor={currentColor}
                setCurrentColor={setCurrentColor}
                currentThickness={currentThickness}
                setCurrentThickness={setCurrentThickness}
                onClearClicked={onClearClicked}
                onSaveClicked={onSaveClicked}
                onCancel={onCancel}
                translation={translation}
            />
            <AnnotationCanvas
                screenshot={screenshot}
                imageSize={imageSize}
                setImageSize={setImageSize}
                setCursorPosition={setCursorPosition}
                imageCanvasRef={imageCanvasRef}
                freehandCanvasRef={freehandCanvasRef}
            />
        </div>
    );
};
