import { ReactElement, createElement } from "react";

import { FormGroupProps } from "typings/FormGroupProps";
import { SCOPE_PREFIX } from "../config";
import Tooltip from "./Tooltip";
import classNames from "classnames";

const FormGroup = ({
    children,
    id,
    errorText,
    labelText,
    hasError,
    className = "",
    tooltipText = ""
}: FormGroupProps): ReactElement => {
    return (
        <div
            className={classNames(
                "form-group no-columns",
                `${SCOPE_PREFIX}-form-group`,
                className,
                hasError && "has-error"
            )}
            aria-label={`Feedback ${labelText}`}
        >
            <div className={`${SCOPE_PREFIX}-label-group`}>
                <label className={`control-label ${SCOPE_PREFIX}-label-group__label`} id={`${id}_label`}>
                    {labelText}
                </label>
                {tooltipText && <Tooltip tip={tooltipText} />}
            </div>

            {children}

            {hasError && (
                <div className="alert alert-danger mx-validation-message" role="alert">
                    {errorText}
                </div>
            )}
        </div>
    );
};

export default FormGroup;
