import { Fragment, ReactElement, createElement, useEffect, useState } from "react";
import { ListAttributeValue, ListValue } from "mendix";

import FeedbackStage from "./FeedbackStage";
import Lightbox from "./Lightbox";
import { SCOPE_PREFIX } from "../config";
import { Stage } from "../utils/enums";
import StartButton from "./StartButton";
import { useFormData } from "../hooks/useFormData";

export type FeedbackStageType = "FORM" | "SCREENSHOT" | "ANNOTATE" | "RESULT" | "FAILED" | "NULL";

interface RootProps {
    className: string;
    tabIndex?: number;
    sprintrapp: string;
    feedbackServerLocation: string;
    allowScreenshot: boolean;
    foreignObjectRendering: boolean;
    scrollableAreaSelector: string;
    translation: Record<string, string | undefined>;
    submitSuccessImgUrl: string;
    submitFailedImgUrl: string;
    userInfoEntityDS?: ListValue;
    displayNameAttribute?: ListAttributeValue<string>;
    emailAddressAttribute?: ListAttributeValue<string>;
}

const FeedbackRoot = ({
    className,
    tabIndex,
    allowScreenshot,
    scrollableAreaSelector,
    foreignObjectRendering,
    userInfoEntityDS,
    displayNameAttribute,
    emailAddressAttribute,
    sprintrapp,
    feedbackServerLocation,
    translation,
    submitSuccessImgUrl,
    submitFailedImgUrl
}: RootProps): ReactElement => {
    const [stage, setStage] = useState<Stage>(Stage.NULL);
    const [formData, setFormData] = useFormData(sprintrapp);
    const [screenshot, setScreenshot] = useState<string>("");
    const [error, setError] = useState<string>("");
    const [showLightbox, setShowLightbox] = useState(false);

    useEffect(() => {
        /*
        There is an issue when a pop-up modal page is displayed in Mendix.
        The mendix modal underlying code set the modal page as `focus-capturing` element
        In that case every focus will be redirected to the `focus-capturing` element which will be mendix modal
        This behavior has conflict with Feedback modal
        To fix the issue here we remove the `focus-capturing` attribute from mendix Modal elements
        We set the original value back again on unmounting the Feedback modal
        */

        if (stage === Stage.NULL) {
            return;
        }

        const capturingElements = document.querySelectorAll("[data-focus-capturing=modal]");

        capturingElements.forEach((element: Element) => {
            element.removeAttribute("data-focus-capturing");
        });

        return () => {
            capturingElements.forEach((element: Element) => {
                element.setAttribute("data-focus-capturing", "modal");
            });
        };
    }, [stage]);

    const onUpdateScreenshot = (screenshot: string): void => {
        setScreenshot(screenshot);
        if (screenshot) {
            setStage(Stage.ANNOTATE);
        } else {
            setStage(Stage.FORM);
        }
    };

    const onCancelAnnotationAndRemoveScreenshot = () => {
        setScreenshot("");
        setStage(Stage.FORM);
    };

    const getStageName = () => {
        switch (stage) {
            case Stage.FORM:
                return "FORM" as FeedbackStageType;
            case Stage.SCREENSHOT:
                return "SCREENSHOT" as FeedbackStageType;
            case Stage.ANNOTATE:
                return "ANNOTATE" as FeedbackStageType;
            case Stage.RESULT:
                return "RESULT" as FeedbackStageType;
            case Stage.FAILED:
                return "FAILED" as FeedbackStageType;
            case Stage.NULL:
                return "NULL" as FeedbackStageType;
            default:
                return "NULL" as FeedbackStageType;
        }
    };

    return (
        <Fragment>
            <StartButton
                onClick={() => setStage(Stage.FORM)}
                className={`${SCOPE_PREFIX}-start-button ${className}`}
                tabIndex={tabIndex}
                translation={translation}
            ></StartButton>
            {showLightbox && (
                <Lightbox
                    onCloseLightbox={() => setShowLightbox(false)}
                    screenshot={formData.screenshot ? formData.screenshot : screenshot}
                />
            )}
            {(stage === Stage.FORM || stage === Stage.RESULT) && (
                <div className={`mx-underlay ${SCOPE_PREFIX}-underlay`} data-html2canvas-ignore="true"></div>
            )}
            <FeedbackStage
                allowScreenshot={allowScreenshot}
                scrollableAreaSelector={scrollableAreaSelector}
                foreignObjectRendering={foreignObjectRendering}
                sprintrapp={sprintrapp}
                userInfoEntityDS={userInfoEntityDS}
                displayNameAttribute={displayNameAttribute}
                emailAddressAttribute={emailAddressAttribute}
                feedbackServerLocation={feedbackServerLocation}
                translation={translation}
                submitSuccessImgUrl={submitSuccessImgUrl}
                submitFailedImgUrl={submitFailedImgUrl}
                stageName={getStageName()}
                setStage={setStage}
                setScreenshot={setScreenshot}
                screenshot={screenshot}
                onCancelAnnotationAndRemoveScreenshot={onCancelAnnotationAndRemoveScreenshot}
                onUpdateScreenshot={onUpdateScreenshot}
                setShowLightbox={setShowLightbox}
                showLightbox={showLightbox}
                setError={setError}
                updateFormData={setFormData}
                formData={formData}
                error={error}
            ></FeedbackStage>
        </Fragment>
    );
};

export default FeedbackRoot;
