import { getScroll } from "../getCursorPosition";

const loadLibrary = async (): Promise<any> => {
    const html2canvas = await import("html2canvas");
    return html2canvas.default;
};

const thirdPartyCapture = async (selector: string, foreignObjectRendering: boolean): Promise<string> => {
    const html2canvas = await loadLibrary();
    /**
     *  1. user-provided selector
     *  2. default Atlas scrollable area selector
     *  3. if document.querySelector returns nothing, use document.body instead
     */
    const { x, y } = getScroll(selector);

    const canvas: HTMLCanvasElement = await html2canvas(document.documentElement, {
        /**
         * https://github.com/niklasvh/html2canvas/issues/1295
         * If foreignObjectRendering is true, onClone will not be triggered.
         * Then this introduced the scrolling issue
         */
        foreignObjectRendering,
        onclone: (clone: Document) => {
            const element = clone.querySelector(selector) || clone.body;
            element.scrollTop = y;
            element.scrollLeft = x;
        }
    }).catch(() => {
        // eslint-disable-next-line no-console
        console.warn(`
            Feedback Widget >> An error occurred while capturing screenshot.
            It is an known issue of a library (html2canvas) that is used in the Feedback widget.
        `);
        // as a fallback: take a screenshot that scrolling is ignored.
        return html2canvas(document.body, {
            foreignObjectRendering,
            windowHeight: document.querySelector("div.mx-page")?.clientHeight,
            scrollY: 0
        });
    });

    const dataUrl = canvas.toDataURL();
    if (canvas && canvas.remove) {
        canvas.remove();
    }

    return dataUrl;
};

export default thirdPartyCapture;
